.project-icon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.project-icon .isvg {
  height: 7em;
  width: 7em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.project-icon .square svg {
  max-height: 55%;
}

.project-icon .round svg {
  max-height: 63%;
}

.project-icon svg {
  filter: url(grey.svg#grayscale) contrast(0);
  /* Firefox 3.5+ */
  -webkit-filter: grayscale(1) contrast(0);
  /* Google Chrome & Safari 6+ */

  -webkit-transition: all .5s ease;

  -o-transition: all .5s ease;

  transition: all .5s ease;
  max-height: 70%;
  max-width: 120%;
}

.project-icon svg:hover {
  filter: none;
  -webkit-filter: grayscale(0%);
  cursor: pointer;
}

.modal-0-2 {
  padding: 1em;
}

.modal-0-2 .modal-content {
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}

.modal-0-2 .modal-content img {
  display: block;
  max-width: 100%;
  margin: auto;
  max-height: 300px;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}

.modal-0-2 .modal-content a {
  margin-right: .5em;
}

.modal-0-2 .loader {
  background: gray;
}



