.headroom {
  background: #FAFAFA;
  z-index: 100 !important;
}

.headroom--unfixed {
  position: absolute !important;
}

.headroom--unfixed .navbar {
  -webkit-box-shadow: none;
          box-shadow: none;
}

.headroom--unfixed .progress-bar {
  display: none;
}

.navbar {
  padding-top: .6em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -webkit-box-shadow: 0 .2em .2em 0 rgba(0,0,0,0.10);
          box-shadow: 0 .2em .2em 0 rgba(0,0,0,0.10);
}

.navbar a {
  display: inline-block;
  padding: .6em 2em .8em;
}

.navbar .next {
  padding: .6em 2em .8em 2em;
  display: -ms-flexbox;
  display: flex;
}

.navbar .next:hover {
  cursor: pointer;
}

.navbar .next a {
  padding: 0
}

.navbar .next .next-arrow {
  height: 1em;
  width: 1em;
  margin-left: 4px;
  -webkit-transition: all .3s linear;
  -o-transition: all .3s linear;
  transition: all .3s linear;
  margin-right: 2em
}

.navbar .next:hover .next-arrow{
  -webkit-transform: translateX(3px);
      -ms-transform: translateX(3px);
          transform: translateX(3px);
}

.nav .progress-bar {
  -webkit-appearance: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 0.4rem;
  border: 0;
  background-color: transparent;
  color: rgb(97, 13, 255);
  z-index: 999;
}

.progress-bar.red {
  color: #EC6565;
}

.progress-bar.blue {
  color: #7CE1EC;
}

.navbar .progress-bar {
  position: absolute;
  width: 100%;
}

progress::-webkit-progress-bar {
  background: #FAFAFA;
}

progress::-webkit-progress-value {
  background: rgb(97, 13, 255);
}

progress.red::-webkit-progress-value {
  background-image: -webkit-gradient(linear, left top, right top, from(#B36FE8), to(#EC6565));
  background-image: -webkit-linear-gradient(left, #B36FE8 0%, #EC6565 100%);
  background-image: linear-gradient(90deg, #B36FE8 0%, #EC6565 100%);
}

progress.blue::-webkit-progress-value {
  background-image: -webkit-gradient(linear, left top, right top, from(#330867), to(#30cfd0));
  background-image: -webkit-linear-gradient(left, #330867 0%, #30cfd0 100%);
  background-image: linear-gradient(90deg, #330867 0%, #30cfd0 100%);
}

progress::-moz-progress-bar {
  background-color: rgb(97, 13, 255);
}

progress.red::-moz-progress-bar {
  background-image: linear-gradient(90deg, #B36FE8 0%, #EC6565 100%);
}

progress.blue::-moz-progress-bar {
  background-image: linear-gradient(90deg, #A06FE8 0%, #7CE1EC 100%);
}


